%heading {
    color: var(--wp--color--normal-text);
    font-style: normal;
    font-weight: 400;
    line-height: 1;
    text-transform: uppercase;
}

%paragraph {
    color: var(--wp--color--normal-text);
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.33;
}


h1 {
    @extend %heading;
    position: relative;
    z-index: 1;
    font-size: 102px;
    line-height: 1;

    &:after {
        position: absolute;
        content: '';
        display: block;
        left: -55px;
        top: -16px;
        width: 220px;
        height: 155px;
        z-index: -1;
        background: url(data:image/svg+xml;base64,) center center no-repeat;
        background-size: contain;
    }

    @include max-break('1024px') {
    }

    strong {
        font-weight: 700;
    }
}

h2 {
    @extend %heading;
    font-size: 48px;
    margin-bottom: 48px;
    font-weight: 700;

    @include min-break('1024px') {
        //font-size: 74px;
    }
}

h3 {
    @extend %heading;
    font-size: 40px;

    @include min-break('1024px') {
        //font-size: 70px;
    }
}

h4 {
    @extend %heading;
    font-size: 32px;

    @include min-break('1024px') {
        //font-size: 50px;
        //line-height: 104.02%;
    }
}

h5 {
    @extend %heading;
    font-size: 24px;

    @include min-break('1024px') {
        //font-size: 45px;
    }
}

h6 {
    @extend %heading;
    font-size: 20px;

    @include min-break('1024px') {
        //font-size: 40px;
    }
}

p {
    @extend %paragraph;
}

strong {
    font-weight: 700;
}

